.App-header{
  display: flex;
  background: #090909;
  color:#fff;
  font-size: 0.75rem;
  padding-left: 20px;
  width: 100vw;
}

div {
  box-sizing: border-box
}

.list {
  padding: 16px 20px;
  border:1px dashed #ccc;
}

h3 span {
  font-size: 0.5rem;
  font-weight: normal;
}

h3 span:first-child {
  cursor: pointer;
}